/* App.css */

body {
  font-family: 'Arial', sans-serif;
  background-color: #00338d; /* Buffalo Bills royal blue background */
  color: #ffffff; /* White text */
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}

.App {
  text-align: center;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 20px rgba(255, 255, 255, 0.2);
  background: linear-gradient(to right, #00338d, #c8102e); /* Buffalo Bills gradient (royal blue to red) */
  width: 90%;
  max-width: 400px;
  margin: 0 auto;
  border: 4px solid #ffffff; /* White border */
  animation: borderAnimation 2s infinite alternate;
}

@keyframes borderAnimation {
  0% {
    border-color: #ffffff;
  }
  100% {
    border-color: #00338d; /* Buffalo Bills royal blue border on animation */
  }
}

button {
  font-size: 16px;
  padding: 15px 20px;
  margin: 10px 5px;
  cursor: pointer;
  border: none;
  border-radius: 8px;
  background: linear-gradient(to right, #00338d, #c8102e); /* Buffalo Bills gradient (royal blue to red) */
  color: #ffffff;
  transition: background-color 0.3s ease-in-out;
}

button:hover {
  background: linear-gradient(to right, #c8102e, #870619); /* Darker red on hover */
}

.counts-container,
.trending-container {
  margin-top: 20px;
}

ul {
  list-style: none;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

li {
  margin: 10px;
  padding: 5px; /* Adjusted padding for smaller size */
  font-size: 12px; /* Smaller font size */
  color: #ffffff;
  background: linear-gradient(to right, #00338d, #c8102e); /* Buffalo Bills gradient (royal blue to red) */
  border-radius: 8px;
  flex: 1 0 30%;
}

/* Reset button style */
.reset-button {
  font-size: 16px;
  padding: 15px 20px;
  margin-top: 20px;
  cursor: pointer;
  border: 4px solid #ffffff; /* White border */
  border-radius: 8px;
  background: linear-gradient(to right, #00338d, #c8102e); /* Buffalo Bills gradient (royal blue to red) */
  color: #ffffff;
  transition: background-color 0.3s ease-in-out;
}

.reset-button:hover {
  background: linear-gradient(to right, #c8102e, #870619); /* Darker red on hover */
}

